import { inject } from '@angular/core';
import { CanActivateFn, Router } from '@angular/router';
import { CookieService } from './cookie.service';
import * as jwt_decode from 'jwt-decode';

export const dashboardGuard: CanActivateFn = (_route, _state) => {
  const cookieService = inject(CookieService);
  const router = inject(Router);
  const query =
    router.getCurrentNavigation()?.extractedUrl['queryParams']['userData'];

  const setUserDetails = (data: any) => {
    for (const key in data) {
      if (data.hasOwnProperty(key)) {
        if (typeof data[key] === 'object') {
          cookieService.set(key, JSON.stringify(data[key]));
        } else {
          cookieService.set(key, data[key]);
        }
      }
    }
  };

  if (!cookieService.get('token')) {
    if (query) {
      const stringifiedData = atob(query as string);
      const userData = JSON.parse(stringifiedData);
      const token = userData['token'];

      if (token) {
        const decodedToken: { exp: number } = jwt_decode.jwtDecode(token);
        if (Date.now() < decodedToken?.exp * 1000) {
          setUserDetails(userData);
          return true;
        } else {
          router.navigate(['']);
          return false;
        }
      } else {
        return false;
      }
    } else {
      router.navigate(['']);
      return false;
    }
  } else {
    const token = cookieService.get('token') || '';
    const decodedToken: { exp: number } = jwt_decode.jwtDecode(token);
    if (Date.now() < decodedToken.exp * 1000) {
      return true;
    } else {
      router.navigate(['']);
      return false;
    }
  }
};
